<template>
  <div class="register">
    <div class="reg_box">
      <p>
        <label for="phone" class="iconfont">&#xe6a6;</label>
        <input type="text" v-model="phone" id="phone" @blur="regphone"  placeholder="请输入手机号码">
      </p>
      <p>
        <label for="password" class="iconfont">&#xe667;</label>
        <input type="text" name="" id="VerCode" placeholder="请输入验证码">
        <span class="huoqu">获取验证码</span>
      </p>
      <p>
        <label for="password" class="iconfont">&#xe632;</label>
        <input type="text" name="" id="password" placeholder="请输入密码">
      </p>
      <button>注册</button>
    </div>

    <div class="xieyi">
      <p>点击注册代表您同意<span @click="xieyishow = true">《用户服务协议》</span></p>
    </div>

    <div class="xieyi_bg" v-show="xieyishow"></div>
    <div class="xieyi2" v-show="xieyishow">
      <h4>《用户服务协议》</h4>
      <div class="xieyi_text">
        <p>下述条款为格式条款，任何用户（注册、登录或使用猫咪惠购app的个人为本协议所述用户，下同）或拟注册用户均须逐字阅读和理解下述协议的条款，并请特别注意加粗、加下划线或以非黑色突出显示的内容。如果对下述条款存在任何疑问，均可请根据本app列明的联系方式提出并由猫咪惠购工作人员予以解释，在猫咪惠购工作人员回复之前，且取得清晰且完全的解释并使用户或拟注册用户排除疑问之前，请暂停注册或使用本app。</p>
      <p>接受和遵守下述条款是注册为“猫咪惠购”用户和使用“猫咪惠购”服务的条件和前提。注册为“猫咪惠购”用户和使用“猫咪惠购”服务即视为接受对下述条款的接受。不接受或不完全接受下述条款，不得注册为“猫咪惠购”用户和使用“猫咪惠购”服务。</p>
      <p>本条款可由猫咪惠购随时更新，更新后的协议条款一旦公布即代替原来的协议条款，恕不再另行通知，用户可在本APP中查阅最新版协议条款。在修改协议条款后，如果用户不接受修改后的条款，请立即停止使用猫咪惠购提供的服务，用户继续使用猫咪惠购提供的服务将被视为接受修改后的协议。</p>
      </div>

    </div>
    <div class="xieyi_close iconfont" v-show="xieyishow" @click="xieyishow = false">&#xe61b;</div>
  </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  export default {
    name:'Register',
    data(){
      return{
        xieyishow : false,
        phone:''
      }
    },
    created(){
      
    },
    methods:{
      regphone(){
        var reg=/^[0-9]+.?[0-9]*$/;
        var phon=this.phone
        if(phon == "")
        {
          // alert("手机号不能为空");
          Toast({
            message: '手机号不能为空',
            duration: 5000
          });
          return false;
        }else if(!reg.test(phon))
        {
          Toast({
            message: '请输入正确的手机号',
            duration: 5000
          });
          return false;
        }else if(phon.length != 11)
        {
          Toast({
            message: '请输入正确的手机号',
            duration: 5000
          });
          return false;
        }
      }
    }

  }
   
</script>

<style scoped>
  .register{
    height: 100vh;
    /* background-color: cornflowerblue; */
    background: linear-gradient(to top,white,white,cornflowerblue);
    /* background: linear-gradient(to top,white,cornflowerblue); */
    position: relative;
    padding-top: 200px;
  }
  .reg_box{
    padding: 25px;
    /* background-color: white; */
    /* margin-top: 200px; */
  }
  .reg_box label{
    color: rgb(21, 95, 233);
    margin-right: 10px;
    font-size: 1.3rem;
    text-shadow: 0px 0px 3px white;
  }
  .reg_box p{
    width: 100%;
    padding: 10px 0;
    border-bottom: 2px solid rgb(197, 215, 248);
  }
  .reg_box input{
    outline: none;
    border: 0;
    font-size: 0.875rem;
    /* color: #ddd; */
    background-color: transparent;
    
  }
  .reg_box button{
    width: 100%;
    outline: none;
    border: 0;
    background-color: cornflowerblue;
    color: white;
    padding: 8px;
    border-radius: 50px;
    box-shadow: 0px 0px 4px cornflowerblue;
    margin-top: 20px;
  }

  .huoqu{
    font-size: 0.875rem;
    color: white;
    background-color: rgb(32, 99, 223);
    padding: 5px 10px;
    border-radius: 50px;
    float: right;
  }


  .xieyi{
    width: 100%;
    position: absolute;
    bottom: 20px;
    left: 0;
    text-align: center;
    
  }
  .xieyi p{
    width: 1005;
    
    color: #aaa;
    font-size: 0.75rem;
  }
  .xieyi span{
    font-size: 0.75rem;
    color: rgb(53, 116, 233);
  }
  .xieyi_bg{
    width: 100%;
    height: 100vh;
    background-color: rgba(1, 1, 1, 0.5);
    position: absolute;
    left: 0;
    top: 0;
  }
  .xieyi2{
    width: 90%;
    height: 60%;
    position: fixed;
    top: 20%;
    left: 5%;
    /* overflow: auto; */
    padding: 20px;
    background-color: white;
    border-radius: 10px;
  }
  .xieyi2 h4{
    text-align: center;
  }
  .xieyi_text{
    height: 94%;
    /* background-color: teal; */
    overflow: auto;

  }
  .xieyi2 p{
    margin: 10px 0;
  }
  .xieyi_close{
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    position: absolute;
    bottom: 13%;
    left: 45%;
    /* background-color: salmon; */
    font-size: 1.5rem;
    color: white;
  }
</style>



